@import '../../styles/main.scss';

.categories {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 128px;

  &__content {
    margin-top: 64px;
    @include page-grid;
    width: 100%;
    gap: 20px;
    height: 480px;
  }

  &__category {
    overflow: hidden;
    grid-column: span 12;
    background-color: $color-warm-yellow;
    border-radius: 40px;
    border: 4px solid $color-white;
    padding: 24px;
    padding-bottom: 0px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    cursor: pointer;
    transition: background-color 0.3s linear;

    &:hover {
     // background-color: $color-orange-hover;

     // .categories__category__title {
    //    color: $color-white;
    //  }
      .categories__category__photo {
        transform: scale(1.1);
      }
    }

    &__title {
      @extend %h3;
      color: $color-dark-grey;
      text-align: center;
    }

    &__photo {
      width: 100%;
      height: 354px;
      overflow: hidden;
      display: flex;
      justify-content: center;
      transition: transform 2s ease;
    }

    &__dogs {
      max-width: 100%;
      height: auto;
    }
  }

  &__footer {
    margin-top: 56px;
  }
}